<template>
  <div>
    <h3 align="center">亲友需求收集</h3>
    <div class="form-group" id="form-input">
      <span>
        <label for="inputName">联系人</label>
        <input type="text" class="form-control" id="inputName" v-model="name" placeholder="Jane Doe" />
        <label for="inputPhone">手机号</label>
        <input type="email" class="form-control" id="inputPhone" v-model="phone" placeholder="157 **** 5588" />
<!--      </span>
      <span> -->
        <label for="inputAddress">地址</label>
        <input type="email" class="form-control" id="inputAddress" v-model="address" placeholder="平顶山市叶县**村" />
      </span>
        <span style="color: green;">注：品种、数量每次添加一种，<br />填写后点击添加，即可再次添加</span>
      <div id="product_list">
        <div id="line0" class="product">
          <label>所需品种</label>
          <input class="form-control kind" id="inputKind0" v-model="kind" placeholder="BN317" />
          <label>数量</label>
          <input class="form-control num" id="inputNum0" v-model="num" placeholder="50斤或5袋" />
          <label for="inputContent">备注</label>
          <input type="email" class="form-control" id="inputContent" v-model="content" placeholder="备注：此品种若有需求,请添加备注!" />
        </div>
      </div>
      <div class="add_product_info">
          <table class="table table-condensed">
            <div v-for="(item, index) in $data.product">
            <tr id="add-info">
              <!-- <th>已添加</th> -->
              <td><div>{{ index + 1}}、</div></td>
              <!-- <th>品种</th> -->
              <td><div>{{ item.kind}}</div></td>
              <!-- <th>数量</th> -->
              <td><div>{{ item.num}}</div></td>
              <!-- <th>备注</th> -->
              <td><div>{{ item.content}}</div></td>
            </tr>
        </div>
          </table>
      </div>
      <div>
      <div><br></div>
        <button type="button" class="btn btn-success" id="button_add" @click="add_product()">添加</button>
        <button type="button" class="btn btn-danger" id="button_rm" @click="rm_product()">删除</button>
      </div>
        <!-- <span style="color: green;">注：所需要的信息填写完毕后点击提交</span> -->
    </div>
      <div><br><br></div>
      <button type="button" class="btn btn-success" @click="submit()">提交信息</button>
  </div>
</template>

<script>
  export default {
    name: 'App',
    data: function () {
      return {
          id: 1,
          url: "http://seeds.sarmn.cn:5050/api/seed/",
          key: "add_info",
          name: "",
          phone: "",
          product: [],
          address: "",
          content: "",
          kind:"",
          num:""
      }
    } ,
    methods: {

      submit() {
        if (! this.name) {
          alert("姓名忘了填写！");
          return 0
        }
        if (!this.phone) {
          alert("联系方式忘了填写！");
          return 0
        }
        if (!this.address) {
          alert("地址忘了填写！");
          return 0
        }
        if ( !this.product.length ) {
            alert("请至少添加一种！");
            return 0
        }
        const params = {
          key:this.key,
          name:this.name,
          phone:this.phone,
          address:this.address,
          product:this.product
        }
        const headers = {
          "Content-Type":"application/json"
        }
        this.axios.post(this.url, {
          params:params,
          headers:headers
        }).then(response => {
          const resp = response.data;
          if (resp.code == 200) {
            alert("信息登记成功！点击返回")
            // this.$router.go(0)
            this.name = "";
            this.phone = "";
            this.product = [];
            this.address = "";
            this.content = "";
            this.kind = "";
            this.num = ""
          }else{
            let msg = "信息登记出现错误：" + resp.msg
            alert(msg)

          }

        })
      },

      add_product() {
      if (this.kind=="") {
          alert("所填种类信息不能为空！")
      }else{
        if (this.num==0){
          alert("所填数量信息不能为空！")
        }else {
          this.product.push({"kind":this.kind,"num":this.num, "content":this.content})
          this.kind = "";
          this.num = "";
          this.content = ""
        }
      }
      },
      rm_product(){
        if (this.product.length == 0){
          alert("没有找到需要删除的数据！")
        }else {
          this.product.pop()
        }
      }
    }
  }
</script>

<style>
  label {
    float: left;
  }

/*  .add_product_info {
    background-color: #ddd;
  } */

 #form-input input {
    background-color:whitesmoke;
  }

/* #add-info {
    width: 100px;
    margin-bottom: auto;
  }
 */

</style>



